import Vue from 'vue'
import Router from 'vue-router'
import ClickIn from '../views/ClickIn'
import Subsidy from '../views/Subsidy'
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'ClickIn',
      component: ClickIn
    },
    {
      path: '/subsidy',
      name: 'Subsidy',
      component: Subsidy
    }
  ]
})



