<script>
// @ is an alias to /src
import qs from 'qs'
export default {
  name: 'subsidy',
  components: {
  },
  data: function () {
    return {
      pickerOptions: {
        disabledDate (time) {
          return time.getMonth() >= new Date().getMonth()
        }
      },
      subsidy: {
        userId: '',
        yearMonth: '' + new Date().getFullYear() + (new Date().getMonth() < 10 ? '0' + new Date().getMonth() : new Date().getMonth()),
        subsidy: 0
      },
      showData: false
    }
  },
  methods: {
    querySubsidy: function () {
      let userId = this.subsidy.userId
      let yearMonth = this.subsidy.yearMonth
      let that = this
      this.$axios.post('/querySubsidy', qs.stringify({ userId: userId, yearMonth: yearMonth }))
        .then(function (response) {
          console.log(response)
          if (response.data.code === 200) {
            that.subsidy.subsidy = response.data.data
            that.showData = true
            that.alertSuccess()
          } else {
            that.showData = false
            that.alertError()
          }
        })
        .catch(function (err) {
          console.log(err)
          that.showData = false
          that.alertError()
        })
    },
    alertSuccess: function () {
      this.$message.success('查询成功')
    },
    alertError: function () {
      this.$message.error('OMG！出现异常情况')
    },
    goBack: function () {
      this.$router.push('/')
    }
  }
}
</script>

<template>
  <el-row :gutter="20">
    <el-col :span="8">
      <div class="grid-content">
        <p></p>
      </div>
    </el-col>
    <el-col :span="8">
      <div class="grid-content cin-over">
        <el-container>
          <!-- header -->
          <el-header style="height:auto">
            <el-row class="cin-title">
              <i class="el-icon-back"
                 style="float:left;cursor:pointer;"
                 @click="goBack"></i>
              补贴查询
            </el-row>
            <div class="bg-bar"></div>
          </el-header>
          <!-- main -->
          <el-main class="cin-main">
            <el-row>
              <el-input v-model="subsidy.userId"
                        style="width:200px;"
                        type='number'
                        placeholder="请输入员工号"></el-input>
              <el-date-picker v-model="subsidy.yearMonth"
                              type="month"
                              value-format="yyyyMM"
                              :picker-options="pickerOptions"
                              placeholder="选择月">
              </el-date-picker>
              <el-button type="primary"
                         style="margin-left:20px"
                         icon="el-icon-search"
                         @click="querySubsidy">点击查询</el-button>
            </el-row>
            <el-row>
              <el-table v-show="showData"
                        :data="[subsidy]"
                        style="width: 100%">
                <el-table-column prop="yearMonth"
                                 label="月份"
                                 width="180">
                </el-table-column>
                <el-table-column prop="userId"
                                 label="员工号"
                                 width="180">
                </el-table-column>
                <el-table-column prop="subsidy"
                                 label="补贴">
                </el-table-column>
              </el-table>

            </el-row>

          </el-main>
          <!-- footer -->
          <el-footer style="height:auto">
          </el-footer>
        </el-container>
      </div>
    </el-col>
    <el-col :span="8">
      <div class="grid-content">
        <p></p>
      </div>
    </el-col>
  </el-row>
</template>

<style scoped>
.cin-over {
  border: 2px #eeeeee solid;
}
.bg-bar {
  height: 250px;
  background-image: url("../assets/img/bg.jpg");
  background-size: cover;
}
.cin-main {
  height: 500px;
  /* display: flex;
  align-items: center;
  justify-content: center; */
}
.cin-btn {
  height: 120px;
  width: 120px;
  font-size: 20px;
}
.cin-title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700;
}
</style>



