<script>
// @ is an alias to /src
import qs from 'qs'
export default {
  name: 'clickin',
  components: {
  },
  data: function () {
    return {
      userId: '',
      status: 1
    }
  },
  methods: {
    clockin: function () {
      let userId = this.userId
      let that = this
      this.$axios.post('/clockin', qs.stringify({ userId: userId }))
        .then(function (response) {
          console.log(response)
          if (response.data.data !== 2 && response.data.data !== 6) {
            that.status = 2 // 打卡成功
            that.alertSuccess()
          } else {
            that.status = 3 // 打卡失败
            that.alertError()
          }
        })
        .catch(function (err) {
          console.log(err)
          that.alertError()
        })
    },
    goSubsidy: function () {
      this.$router.push('/subsidy')
    },
    alertSuccess: function () {
      this.$message.success('打卡成功')
    },
    alertError: function () {
      this.$message.error('OMG！打卡失败')
    }
  }
}
</script>

<template>
  <el-row :gutter="20">
    <el-col :span="8">
      <div class="grid-content">
        <p></p>
      </div>
    </el-col>
    <el-col :span="8">
      <div class="grid-content cin-over">
        <el-container>
          <!-- header -->
          <el-header style="height:auto">
            <el-row class="cin-title">考勤打卡</el-row>
            <div class="bg-bar"></div>
          </el-header>
          <!-- main -->
          <el-main class="cin-main">
            <el-input v-model="userId"
                      style="margin-bottom:20px;width:200px;"
                      type='number'
                      placeholder="请输入员工号"></el-input>
            <el-button v-if="status==1"
                       type="warning"
                       class="cin-btn"
                       circle
                       @click="clockin">
              打卡
            </el-button>
            <el-button v-if="status==2"
                       type="success"
                       class="cin-btn"
                       icon="el-icon-check"
                       circle></el-button>
            <el-button v-if="status==3"
                       type="danger"
                       class="cin-btn"
                       icon="el-icon-close"
                       circle></el-button>
          </el-main>
          <!-- footer -->
          <el-footer style="height:auto;margin-bottom: 20px;">
            <el-link type="primary"
                     @click="goSubsidy">查看补贴</el-link>
          </el-footer>
        </el-container>
      </div>
    </el-col>
    <el-col :span="8">
      <div class="grid-content">
        <p></p>
      </div>
    </el-col>
  </el-row>
</template>

<style scoped>
.cin-over {
  border: 2px #eeeeee solid;
}
.bg-bar {
  height: 250px;
  background-image: url("../assets/img/bg.jpg");
  background-size: cover;
}
.cin-main {
  height: 500px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}
.cin-btn {
  height: 120px;
  width: 120px;
  font-size: 20px;
}
.cin-title {
  margin-top: 20px;
  margin-bottom: 20px;
  font-size: 20px;
  font-weight: 700;
}
</style>



